
export default{
    title: "TaskList",
    data(){ //
        return{
            msg: 'To Do List',
            todos:[
                {text : 'vue-router', done:false, editing:false},
                {text : 'vuex', done:false, editing:false},
                {text : 'vue-loader', done:false, editing:false},
                {text : 'awsome-vue', done:true, editing:false},
            ],
            newTodo:""
        }
    },
    methods: {
        addTodo: function(event){
            let text = this.newTodo.trim()//
            if (!text){
                return  //
            }
            this.todos.push({//push
                text: text,
                done: false
            })
            this.newTodo = ''
        },

        removeTodo: function (event){
            for(let i = this.todos.length-1; i >= 0; i--){//0 ~ (i-1) 
                if(this.todos[i].done) {
                    this.todos.splice(i,1)//i1  splice(index,,)
                }
            }
        }
    },
}


